/***** Grid Variables *****/
[class*="gap"] {
  --gap-1tn: 1px;
  --gap-2tn: 2px;
  --gap-3tn: 5px;
  --gap-1sm: 2px;
  --gap-2sm: 5px;
  --gap-3sm: 10px;
  --gap-1md: 5px;
  --gap-2md: 10px;
  --gap-3md: 20px;
  --gap-1lg: 10px;
  --gap-2lg: 20px;
  --gap-3lg: 50px;
  --gap-1wd: 20px;
  --gap-2wd: 50px;
  --gap-3wd: 100px;
}

/***** Grid & Fluid *****/
[class*="grid"],
[class*="fluid"] {
  display: grid;
}

[class*="grid"] {
  margin: auto;
  max-width: 80%;
}

[class*="fluid"] {
  max-width: 100%;
}

/***** Grid & Fluid Tiny *****/
[class*="grid"][class*="2tn"],
[class*="fluid"][class*="2tn"] {
  grid-template: auto/repeat(2, auto);
}

[class*="grid"][class*="3tn"],
[class*="fluid"][class*="3tn"] {
  grid-template: auto/repeat(3, auto);
}

